<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-顶部导航1">顶部导航</a></li><li><a href="#vue-doc-侧栏导航1">侧栏导航</a></li><li><a href="#vue-doc-内嵌菜单1">内嵌菜单</a></li><li><a href="#vue-doc-分组1">分组</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-menu导航菜单1" class="vue-doc-element">Menu 导航菜单</h1>
<p class="vue-doc-element">为页面和功能提供导航的菜单列表，常用于网站顶部和左侧。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demosidebar></demosidebar>
<demonest></demonest>
<demogroup></demogroup>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-menuprops1_1" class="vue-doc-element">Menu props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>mode</td>
<td>菜单类型，可选值为 horizontal（水平） 和 vertical（垂直）</td>
<td>String</td>
<td>vertical</td>
</tr>
<tr>
<td>theme</td>
<td>主题，可选值为 light、dark、primary，其中 primary 只适用于 mode=&quot;horizontal&quot;</td>
<td>String</td>
<td>light</td>
</tr>
<tr>
<td>active-name</td>
<td>激活菜单的 name 值</td>
<td>String / Number</td>
<td>-</td>
</tr>
<tr>
<td>open-names</td>
<td>展开的 Submenu 的 name 集合</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>accordion</td>
<td>是否开启手风琴模式，开启后每次至多展开一个子菜单</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>width</td>
<td>导航菜单的宽度，只在 mode=&quot;vertical&quot; 时有效，如果使用 Col 等布局，建议设置为 auto</td>
<td>String</td>
<td>240px</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-menuevents1_2" class="vue-doc-element">Menu events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-select</td>
<td>选择菜单（MenuItem）时触发</td>
<td>name</td>
</tr>
<tr>
<td>on-open-change</td>
<td>当 展开/收起 子菜单时触发</td>
<td>当前展开的 Submenu 的 name 值数组</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-menumethods1_3" class="vue-doc-element">Menu methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>updateOpened</td>
<td>手动更新展开的子目录，注意要在 $nextTick 里调用</td>
<td>无</td>
</tr>
<tr>
<td>updateActiveName</td>
<td>手动更新当前选择项，注意要在 $nextTick 里调用</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-menuitemprops1_4" class="vue-doc-element">MenuItem props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>菜单项的唯一标识，必填</td>
<td>String / Number</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-submenuprops1_5" class="vue-doc-element">Submenu props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>子菜单的唯一标识，必填</td>
<td>String / Number</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-submenuslot1_6" class="vue-doc-element">Submenu slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>菜单项</td>
</tr>
<tr>
<td>title</td>
<td>子菜单标题</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-menugroupprops1_7" class="vue-doc-element">MenuGroup props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>分组标题</td>
<td>String</td>
<td>空</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demosidebar from './demo/sidebar.md';
import demonest from './demo/nest.md';
import demogroup from './demo/group.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demosidebar,demonest,demogroup
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


