          <p class="vue-doc-element">垂直导航菜单，可以内嵌子菜单。</p>
<p class="vue-doc-element">设置 active-name 可以选中指定的菜单，设置 open-names 可以展开指定的子菜单。</p>
<p class="vue-doc-element">设置属性 accordion 可以开启手风琴模式，每次只能展开一个子菜单。</p>
<p class="vue-doc-element">通过设置属性 theme 为 light、dark 可以选择主题，侧栏菜单不支持 primary 主题。</p>

