                <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-analytics"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
                Navigation One
            <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
