                <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"1-2"</span>&gt;</span>Option 2<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">MenuGroup</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">MenuGroup</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Item 2"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"1-3"</span>&gt;</span>Option 3<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"1-4"</span>&gt;</span>Option 4<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">MenuGroup</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">Submenu</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Submenu</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"2"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"title"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-filing"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
                Navigation Two
            <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"2-1"</span>&gt;</span>Option 5<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"2-2"</span>&gt;</span>Option 6<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Submenu</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"3"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"title"</span>&gt;</span>Submenu<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"3-1"</span>&gt;</span>Option 7<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"3-2"</span>&gt;</span>Option 8<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
