            <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"4-4"</span>&gt;</span>Option 12<span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
